'use client';

import React from 'react';
import useSWR from 'swr';
import {
    Calendar, ChevronLeft, ChevronRight, Phone,
    MapPin, Filter, MoreVertical
} from 'lucide-react';

const fetcher = (url: string) => fetch(url).then(r => r.json());

export default function ReservationsPage() {
    const { data: reservationsData } = useSWR('/api/reservations', fetcher);
    const reservations = reservationsData?.data || [];

    return (
        <div className="flex flex-1 flex-col overflow-hidden bg-[#050a07] h-full">
            <div className="px-6 py-6 border-b border-[#1e3328] bg-[#0f1a14] flex justify-between items-end">
                <div>
                    <h2 className="text-2xl font-bold text-white mb-1">Jadwal Reservasi</h2>
                    <p className="text-slate-400 text-sm">Kelola booking meja untuk hari ini dan mendatang.</p>
                </div>
                <div className="flex gap-3">
                    <button className="flex items-center gap-2 bg-[#16261d] border border-[#1e3328] text-white px-4 py-2 rounded text-sm font-medium hover:border-[#22c55e] transition-colors">
                        <Filter size={16} /> Filter
                    </button>
                    <button
                        onClick={() => alert("Fitur baru")}
                        className="flex items-center gap-2 bg-[#22c55e] hover:bg-[#16a34a] text-black px-4 py-2 rounded text-sm font-bold transition-colors"
                    >
                        <Calendar size={16} /> Buat Reservasi
                    </button>
                </div>
            </div>

            <div className="flex-1 overflow-y-auto p-6">
                {/* Date Selector */}
                <div className="flex items-center gap-4 mb-6">
                    <button className="p-2 rounded bg-[#16261d] text-slate-400 hover:text-white transition-colors border border-[#1e3328]"><ChevronLeft size={20} /></button>
                    <div className="flex items-center gap-2 text-white font-bold text-lg">
                        <Calendar size={20} className="text-[#22c55e]" />
                        <span>Oktober 2023 (Demo)</span>
                    </div>
                    <button className="p-2 rounded bg-[#16261d] text-slate-400 hover:text-white transition-colors border border-[#1e3328]"><ChevronRight size={20} /></button>
                </div>

                {/* Reservation Table */}
                <div className="rounded border border-[#1e3328] overflow-hidden bg-[#0f1a14]">
                    <table className="w-full text-left border-collapse">
                        <thead className="bg-[#16261d] text-slate-400 text-xs uppercase font-semibold">
                            <tr>
                                <th className="px-6 py-3 border-b border-[#1e3328]">Waktu / Tanggal</th>
                                <th className="px-6 py-3 border-b border-[#1e3328]">Nama Customer</th>
                                <th className="px-6 py-3 border-b border-[#1e3328]">Kontak</th>
                                <th className="px-6 py-3 border-b border-[#1e3328]">Detail</th>
                                <th className="px-6 py-3 border-b border-[#1e3328]">Meja</th>
                                <th className="px-6 py-3 border-b border-[#1e3328] text-center">Status</th>
                                <th className="px-6 py-3 border-b border-[#1e3328] text-right">Aksi</th>
                            </tr>
                        </thead>
                        <tbody className="text-sm divide-y divide-[#1e3328]">
                            {reservations.length === 0 ? (
                                <tr>
                                    <td colSpan={7} className="text-center py-8 text-slate-500">Tidak ada reservasi ditemukan.</td>
                                </tr>
                            ) : (
                                reservations.map((res: any) => (
                                    <tr key={res.id} className="hover:bg-[#16261d]/50 transition-colors">
                                        <td className="px-6 py-4">
                                            <div className="font-bold text-white">{new Date(res.bookingTime).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}</div>
                                            <div className="text-xs text-slate-400">{new Date(res.bookingDate).toLocaleDateString()}</div>
                                        </td>
                                        <td className="px-6 py-4">
                                            <div className="font-medium text-white">{res.customerName}</div>
                                            {res.notes && <div className="text-xs text-[#eab308] mt-1 italic">{res.notes}</div>}
                                        </td>
                                        <td className="px-6 py-4">
                                            <div className="flex items-center gap-2 text-slate-300">
                                                <Phone size={14} className="text-slate-500" />
                                                {res.phone}
                                            </div>
                                        </td>
                                        <td className="px-6 py-4">
                                            <span className="text-slate-300">{res.pax} Org â€¢ {res.tableType}</span>
                                        </td>
                                        <td className="px-6 py-4">
                                            <span className="text-slate-500 italic">Belum assign</span>
                                        </td>
                                        <td className="px-6 py-4 text-center">
                                            <span className="bg-[#22c55e]/20 text-[#22c55e] text-xs px-2 py-0.5 rounded border border-[#22c55e]/20">{res.status}</span>
                                        </td>
                                        <td className="px-6 py-4 text-right">
                                            <button className="text-slate-400 hover:text-white transition-colors"><MoreVertical size={16} /></button>
                                        </td>
                                    </tr>
                                ))
                            )}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    );
}
